import * as React from 'react';
import { Input, Icon, Button } from 'antd';
import {Link} from 'react-router-dom';
import axios from 'src/config/axios'

import './Login.scss'

interface ILoginState {
	account: string,
	password: string
}

class Login extends React.Component<any,ILoginState> {
	constructor(props){
		super(props)
		this.state = {
			account: '',
			password: '',
		}
	}

	onChange = (key: keyof ILoginState,value:string) => {
		const newState = {}
		newState[key] = value
		this.setState(newState)
	}

	submit = async () => {
		const { account,password } = this.state;
		try{
			await axios.post('sign_in/user',{
				account, // account: account
				password
			})
			this.props.history.push('/')
		}catch(e){
			throw new Error(e)
		}
	}

	public render() {
		const { account,password } = this.state;
		return (
			<div className="Login" id="Login">
				<h1></h1>
				<Input
					placeholder=""
					prefix={<Icon type="user" style={{ color: 'rgba(0,0,0,.25)' }} />}
					value={account}
					onChange={(e) => this.onChange('account',e.target.value)}
				/>
				<Input.Password value={password} placeholder="" onChange={(e) => this.onChange('password',e.target.value)}/>
				<Button type="primary" className="loginButton" onClick={this.submit}></Button>
				<p> <Link to="/signUp"></Link></p>
			</div>
		);
	}
}

export default Login;


